import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  getStateText,
  getEmployeeTypeText,
  formatAmount,
} from "../utils/textUtils";
import "../config/index.css";

import { useFetchAccual } from "../utils/useFetchAccual"; //    
import { useFetchYearData } from "../utils/useFetchYearData"; //    

function Dashboard() {
  const navigate = useNavigate();
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
  const { userData, userError, userLoading } = useFetchAccual(); //   
  const { error, loading, totalAccumulatedAmount, fetchDataByYear } =
    useFetchYearData(selectedYear); //   

  const handleEmployeeClick = (employeeId) => {
    navigate(`/dashboard/${employeeId}`); //   
  };

  const handleChangeYear = (event) => {
    setSelectedYear(event.target.value); //     
  };

  const handleSearchClick = () => {
    fetchDataByYear(selectedYear); //    
  };

  //         
  if (userLoading) {
    // || loading
    return <div className="loading">Loading...</div>;
  }

  if (userError || loading) {
    return <div className="error">{userError || error}</div>;
  }

  return (
    <div className="container">
      <h1 className="title">WEAVUS  </h1>
      <h3 className="title">  </h3>
      <div className="year-container">
        <label htmlFor="year" className="label">
           :
        </label>
        <input
            id="year"
            type="number"
            value={selectedYear}
            onChange={handleChangeYear}
            min="1111"
            max="9999"
            // className="input"
        />
        <button className="submit-button" onClick={handleSearchClick}>
          
        </button>
      </div>
      <p className="total-amount">
        {selectedYear}   : {formatAmount(totalAccumulatedAmount)}
      </p>
      <table className="table">
        <thead>
          <tr>
            <th className="table-header"></th>
            <th className="table-header"> </th>
            <th className="table-header"></th>
            <th className="table-header"></th>
            <th className="table-header"> </th>
            <th className="table-header"> </th>
          </tr>
        </thead>
        <tbody>
          {userData.map((item) => (
            <tr key={item.id}>
              <td className="table-data">
                <button
                  className="action-button"
                  onClick={() => handleEmployeeClick(item.employee.id)}
                >
                  {item.employee.name}
                </button>
              </td>
              <td className="table-data">
                {getEmployeeTypeText(
                  item.employee.employeeType,
                  item.employee.status,
                    item.employee.exitDate
                )}
              </td>
              <td className="table-data">{item.employee.entryDate}</td>
              <td className="table-data">
                {item.employee.exitDate ? item.employee.exitDate : ""}
              </td>
              <td className="table-data">
                {formatAmount(item.totalAmount ? item.totalAmount : "0")}
              </td>
              <td className="table-data">{getStateText(item.state)}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Dashboard;
